<template>
  <div>
    <!-- 查询axios时显示的加载进行时 -->
    <van-loading style="margin-top: 100px" size="24px" vertical v-if="loading"
      >加载中...</van-loading
    >
    <!-- 刷新本页 -->
    <van-pull-refresh
      v-model="isLoading"
      success-text="刷新成功"
      @refresh="onRefresh"
      v-else
    >
      <!-- boy主要内容 -->
      <div class="boy">
        <Swiper :images="images" />

        <!-- 类型模块一 -->
        <div class="container">
          <Title title="东西奇幻" :loading="loading" :typeBooks="fantasyNovels"
            >东西奇幻</Title
          >
          <ul v-if="fantasyNovels">
            <BookItemTypeTwo
              :item="fantasyNovels[randomNumSec]"
              :key="fantasyNovels[randomNumSec]._id"
              :loading="loading"
            />
          </ul>
          <ul class="ul-type-one" v-if="fantasyNovels">
            <BookItemTypeOne
              v-for="item in fantasyNovels.slice(0 + randomNum, 4 + randomNum)"
              :item="item"
              :key="item._id"
              :loading="loading"
            />
          </ul>
        </div>

        <!-- 类型模块三 -->
        <div class="container">
          <Title title="悬疑灵异" :loading="loading" :typeBooks="suspenseNovels"
            >悬疑灵异</Title
          >
          <van-grid column-num="3" :center="false" v-if="suspenseNovels">
            <BookItemTypeThree
              v-for="item in suspenseNovels.slice(0 + randomNum, 3 + randomNum)"
              :item="item"
              :key="item._id"
              :loading="loading"
            />
          </van-grid>
        </div>

        <!-- 类型模块二 -->
        <div class="container">
          <Title title="都市热文" :loading="loading" :typeBooks="cityNovels"
            >都市热文</Title
          >
          <ul v-if="cityNovels">
            <BookItemTypeTwo
              v-for="item in cityNovels.slice(0 + randomNum, 6 + randomNum)"
              :item="item"
              :key="item._id"
              :loading="loading"
            />
          </ul>
        </div>

        <!-- 回到顶部 -->
        <BackToTop />
      </div>
    </van-pull-refresh>
  </div>
</template>


<script>
import Swiper from "@/components/Swiper.vue";
import Title from "@/components/Title.vue";
import BookItemTypeOne from "@/components/BookItemTypeOne.vue";
import BookItemTypeTwo from "@/components/BookItemTypeTwo.vue";
import BookItemTypeThree from "@/components/BookItemTypeThree.vue";
import BackToTop from "@/components/BackToTop.vue";
export default {
  components: {
    Swiper,
    Title,
    BookItemTypeOne,
    BookItemTypeTwo,
    BookItemTypeThree,
    BackToTop,
  },
  data() {
    return {
      cityNovels: null,
      fantasyNovels: null,
      suspenseNovels: null,
      randomNum: 0, //每次书籍展示的起点下标
      randomNumSec: 5, //每次书籍展示的起点下标

      maxSearch: 30, //一次最多搜索到的数量
      isLoading: false,
      loading: true, //等待loading布尔值
      loadingCount: 0,
      images: [
        {
          img:
            "https://plf-new.zhuishushenqi.com/management/images/20201218/0d8dc8bbb17a4c9793706ab12d979697.jpg",
          name: "我有一座超级海岛",
          _id:'5dc8bb6e1a9f25ad19391258',
        },
        {
          img:
            "https://plf-new.zhuishushenqi.com/management/images/20201218/12ddcc817cdf49f787959bb53e21b584.png",
          name: "妖道至尊",
          _id:'536da71c88844484410104a3',
        },
        {
          img:
            "https://plf-new.zhuishushenqi.com/management/images/20201218/574d9d74adae46a7a75a3897414cf365.png",
          name: "庆余年",
          _id:'508742f0abf1ced53c000024',
        },
      ],
    };
  },
  watch: {
    loadingCount(n) {
      if (n >= 3) {
        this.loading = false;
      }
    },
  },
  created() {
    this.loading = true;
    this.loadingCount = 0;
    this.searchData();
  },

  mounted() {
    this.getRandomNum();
  },
  methods: {
    // 刷新当前页面的数据
    onRefresh() {
      setTimeout(() => {
        this.isLoading = false;
        this.getRandomNum();
      }, 1000);
      // timer();
    },
    // 获取数据
    searchData() {
      // 都市书籍查询
      // if (localStorage.getItem("cityNovels")) {
      //   this.cityNovels = JSON.parse(localStorage.getItem("cityNovels"));
      //   console.log("都市书籍已存入本地存储");
      //   this.loadingCount++;
      // } else {
      //   this.axios
      //     .get(
      //       "/category-info?gender=male&type=hot&major=都市&minor=&start=0&limit=" +
      //         this.maxSearch
      //     )
      //     .then((res) => {
      //       console.log("都市书籍");
      //       // console.log(res.data.books);
      //       this.cityNovels = res.data.books.slice(0, this.maxSearch);
      //       localStorage.setItem(
      //         "cityNovels",
      //         JSON.stringify(res.data.books.slice(0, this.maxSearch))
      //       );
      //     })
      //     .finally(() => {
      //       this.loadingCount++;
      //     });
      // }

      // 都市书籍查询
      if (
        localStorage.getItem("cityNovels") &&
        JSON.parse(localStorage.getItem("cityNovels")).expire > Date.now()
      ) {
        this.cityNovels = JSON.parse(localStorage.getItem("cityNovels")).books;
        // console.log("都市书籍已存入本地存储");
        this.loadingCount++;
      } else {
        this.axios
          .get(
            "/category-info?gender=male&type=hot&major=都市&minor=&start=0&limit=" +
              this.maxSearch
          )
          .then((res) => {
            // console.log("都市书籍");
            let obj = {
              books: res.data.books.slice(0, this.maxSearch),
              expire: Date.now() + 24 * 3600 * 1000, //设置时效
            };
            this.cityNovels = obj.books;
            localStorage.setItem("cityNovels", JSON.stringify(obj));
          })
          .finally(() => {
            this.loadingCount++;
          });
      }

      // 奇幻书籍查询
      if (
        localStorage.getItem("fantasyNovels") &&
        JSON.parse(localStorage.getItem("fantasyNovels")).expire > Date.now()
      ) {
        this.fantasyNovels = JSON.parse(
          localStorage.getItem("fantasyNovels")
        ).books;
        // console.log("奇幻书籍已存入本地存储");
        this.loadingCount++;
      } else {
        this.axios
          .get(
            "/category-info?gender=male&type=hot&major=奇幻&minor=&start=0&limit=" +
              this.maxSearch
          )
          .then((res) => {
            // console.log("奇幻书籍");
            let obj = {
              books: res.data.books.slice(0, this.maxSearch),
              expire: Date.now() + 24 * 3600 * 1000, //设置时效
            };
            this.fantasyNovels = obj.books;
            localStorage.setItem("fantasyNovels", JSON.stringify(obj));
          })
          .finally(() => {
            this.loadingCount++;
          });
      }

      // 悬疑书籍查询
      if (
        localStorage.getItem("suspenseNovels") &&
        JSON.parse(localStorage.getItem("suspenseNovels")).expire > Date.now()
      ) {
        this.suspenseNovels = JSON.parse(
          localStorage.getItem("suspenseNovels")
        ).books;
        // console.log("悬疑书籍已存入本地存储");
        this.loadingCount++;
      } else {
        this.axios
          .get(
            "/category-info?gender=male&type=hot&major=灵异&minor=&start=0&limit=" +
              this.maxSearch
          )
          .then((res) => {
            // console.log("悬疑书籍");
            let obj = {
              books: res.data.books.slice(0, this.maxSearch),
              expire: Date.now() + 24 * 3600 * 1000, //设置时效
            };
            this.suspenseNovels = obj.books;
            localStorage.setItem("suspenseNovels", JSON.stringify(obj));
          })
          .finally(() => {
            this.loadingCount++;
          });
      }
    },
    // 获取随机数
    getRandomNum() {
      // 获取一个随机数
      this.randomNum = Math.floor(Math.random() * (this.maxSearch - 6));
      this.randomNumSec = Math.floor(Math.random() * this.maxSearch);
    },
  },
};
</script>

