  margin: 90px 0 60px 0;
  padding: 0 10px;
  box-sizing: border-box;
  transition: all 2s;
  .container {
    padding: 10px 3px;
    box-sizing: border-box;
  }
}
.ul-type-one {
  display: flex;
  margin-top: 5px;
}
