<template>
  <van-swipe
    class="my-swipe"
    :autoplay="2000"
    :show-indicators="false"
    :loop="true"
  >
    <van-swipe-item v-for="(item, index) in images" :key="index">
      <van-image width="100%" :src="item.img" @click="toBookdetail(item._id)" />
    </van-swipe-item>
  </van-swipe>
</template>

<script>
export default {
  props: ["images"],
  methods: {
    toBookdetail(id) {
      console.log(id);
      this.$router.push({
        path: "/bookDetail/" + id,
      });
    },
  },
};
</script>

<style lang="less">
  .my-swipe {
    img{
      min-height: 148px;
    }
  }
</style>