<template>
  <div class="title">
    <div class="titleFont">
      <slot></slot>
    </div>
    <span @click="toTypeDetails">查看更多</span>
  </div>
</template>

<script>
export default {
  props: ["typeBooks"],
  methods: {
    toTypeDetails() {
      this.$router.push({
        path: "/typeDetails",
        query: {
          books: JSON.stringify(this.typeBooks),
          loading: this.$attrs.loading,
          title: this.$attrs.title,
        },
      });
    },
  },
};
</script>

<style lang="less">
.title {
  position: relative;
  margin: 15px 0;
  height: 20px;
  .titleFont {
    position: absolute;
    left: 0;
    top: 1px;
    font-size: 16px;
    color: #1a1a1a;
    font-weight: 600;
    font-family: "Arial";
  }
  span {
    position: absolute;
    top: 3px;
    right: 0;
    height: 14px;
    color: cornflowerblue;
    font-size: 12px;
    &::after {
      content: "";
      display: inline-block;
      width: 10px;
      height: 10px;
      margin-left: 5px;
      background: url("../../public/img/rightArrowhead.svg") no-repeat;
    }
  }
}
</style>