<template>
  <!-- 回到顶部 -->
  <div class="roll" @click="toTop">回到顶部</div>
</template>

<script>
export default {
  methods: {
    // 回到顶部
    toTop() {
      var toTopTimer = setInterval(() => {
        document.documentElement.scrollTop =
          document.documentElement.scrollTop - 100;
        if (document.documentElement.scrollTop == 0) {
          // console.log("至顶");
          clearInterval(toTopTimer);
        }
      }, 30);
    },
  },
};
</script>

<style lang="less">
.roll {
  text-align: center;
  font-size: 13px;
  color: #999;
  &::after {
    content: "";
    display: inline-block;
    vertical-align: middle;
    width: 12px;
    height: 12px;
    background: url("../../public/img/topIcon.svg") no-repeat;
    background-size: contain;
    margin-left: 5px;
  }
}
</style>